@use "../base/variables" as v;
@use "../base/mixins" as m;

.header {
  background-color: v.$negro1;
  @include m.tablet {
    &.fijo {
      left: 0;
      top: 0;
      position: fixed;
      background-color: v.$negro1;
      width: 100%;
      box-shadow: 0.1rem 0.2rem 0.3rem #333;
    }
  }

  h1 {
    color: v.$amarillo;
    text-align: center;
    @include m.tablet {
      margin: 0;
    }
  }
  .contenido-header {
    padding: 2rem;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
.navegacion-principal {
  @include m.tablet {
    display: flex;
    gap: 2rem;
  }
  a {
    display: block;
    text-align: center;
    color: v.$blanco;
    font-size: 2.4rem;
    margin-bottom: 1rem;
    @include m.tablet {
      margin: 0;
    }
    &:hover {
      color: v.$amarillo;
      transform: scale(102%);
      transition: 0.2s;
    }
  }
}
